export const SearchNamesById = (estatesArray: any, array: string[]) => {
    estatesArray.map((estateArray: any) => {
        (estateArray.id === 1) 
            ? array[0] = '' 
            : (estateArray.id === 2) ? array[1] = ''
            : (estateArray.id === 3) ? array[2] = '  '
            : (estateArray.id === 4) ? array[3] = '  ' : console.log('fewrwr');
    })
};
