import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import AdapterDateFns from '@mui/lab/AdapterDateFns';
// Components
import { FormControl, InputLabel, MenuItem, Select, TextField} from "@mui/material";
// Store
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
import DateRangePicker, { DateRange } from '@mui/lab/DateRangePicker';
import { GetShortenedTypes } from "../../../Redux/Admin/Analytics/AnalyticsSelector";
// Styles
import styles from './AnalyticsFilter.module.css';
// Thunks
import { getAnalyticsDataThunk, getShortenedTypesThunk } from "../../../Redux/Admin/Analytics/AnalyticsThunk";
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { Box } from "@mui/system";

export const AnalyticsFilter = () => {
    const dispatch = useDispatch();

    // Local store
    const {register, handleSubmit} = useForm<FormTypes>();
    const [category, setCategory] = useState<number | null>(null);
    const [type, setType] = useState<number | null>(null);
    const [dataValue, setDateValue] = useState<any>([null, null]);


    // Global store
    const access_token = useSelector(GetAccessToken);
    const shortened_types = useSelector(GetShortenedTypes);
    

    // Initial actions
    useEffect(() => {
        dispatch(getShortenedTypesThunk(access_token))
    }, [access_token, dispatch]);

    // Functions
    const categoryHandleChange = (event: any) => {
        setCategory(event.target.value);
      };

    const typeHandleChange = (event: any) => {
        setType(event.target.value);
    }

    const onFormSubmit = (data: FormTypes) => {
        debugger;
        const preparedData = {
            min: (data.min === undefined || null) ? data.min : '',
            max: (data.max === undefined || null) ? data.max : '',
            min_area: (data.min_area === undefined || null) ? data.min_area : '',
            max_area: (data.max_area === undefined || null) ? data.max_area : '',
            category: (category === undefined || null) ? category : '',
            type: (type === undefined || null) ? type : '',
            startDate: (dataValue === undefined || null) ? dataValue[0].getDate() + '-' +  (dataValue[0].getMonth() + 1)  + '-' +  dataValue[0].getFullYear() : '',
            endDate: (dataValue === undefined || null) ? dataValue[1].getDate() + '-' +  (dataValue[1].getMonth() + 1)  + '-' +  dataValue[1].getFullYear() : '',
        };
        console.log(preparedData);
        
        debugger;
        dispatch(getAnalyticsDataThunk(preparedData, access_token));
    };

    return (
        <div className={styles.main_block}>
            <LocalizationProvider dateAdapter={AdapterDateFns}>
            <form onSubmit={handleSubmit(onFormSubmit)}>
            <div>
                    <FormControl>
                        <InputLabel id="category"></InputLabel>
                        <Select
                            labelId="category"
                            id="demo-simple-select"
                            label=""
                            value={category}
                            onChange={categoryHandleChange}
                        >
                            <MenuItem value={1}></MenuItem>
                            <MenuItem value={2}></MenuItem>
                        </Select>
                    </FormControl>
                </div>
                <div>
                    <TextField
                        type='number'
                        id="standard-basic"
                        label="min"
                        {...register('min')}
                        variant="standard" />
                </div>
                <div>
                    <TextField
                        id="standard-basic" 
                        type='number' 
                        {...register('max')}
                        label="max" 
                        variant="standard" />
                </div>
                <div>
                    <TextField
                        type='number'
                        id="standard-basic"
                        label=". ."
                        {...register('min_area')}
                        variant="standard" />
                </div>
                <div>
                    <TextField
                        id="standard-basic" 
                        type='number' 
                        {...register('max_area')}
                        label=". ." 
                        variant="standard" />
                </div>
               
                
                <div>
                    <FormControl fullWidth>
                        <InputLabel id="type"></InputLabel>
                        <Select
                            labelId='type'
                            id="type"
                            label=""
                            value={type}
                            onChange={typeHandleChange}
                        >
                            <MenuItem value={1}></MenuItem>
                            <MenuItem value={2}></MenuItem>
                        </Select>
                    </FormControl>
                </div>
                <div>
                    <DateRangePicker
                        // className={styles.}
                        startText=''
                        endText=''
                        value={dataValue}
                        onChange={(newDateValue) => {
                            setDateValue(newDateValue);
                        }}
                        renderInput={(startProps, endProps) => (
                            <>
                                <TextField {...startProps}/>
                                <Box sx={{mx:2}}> to </Box>
                                <TextField {...endProps}/>
                            </>
                        )}
                    />
                </div>
                <button></button>
            </form>
            </LocalizationProvider>
        </div>
    );
};

export type FormTypes = {
    min?: string;
    max?: string;
    min_area?: string;
    max_area?: string;
    startDate?: any;
    endDate?: any;
    category?: any;
    type?: any;
}