import { AxiosResponse } from 'axios';
import { api } from '../../../api';
import { FormTypes } from '../../../Components/Filter/AnalyticsFilter/AnalyticsFilter';
import { Thunk } from '../../types';
import { AnalyticsActions, AnalyticsActionsType } from './AnalyticsReducer';

export const getAnalyticsDataThunk =
    (searchData: FormTypes, token: string): Thunk<AnalyticsActionsType> =>
        async dispatch => {
    debugger;

            try {
                if (token === "") return;
                const response: AxiosResponse<GetResposeType> = await api.getApi({
                    url: `admin/all-real-estate-statistics?price.min=${searchData.min}&price.max=${searchData.max}&area.min=${searchData.min_area}&area.max=${searchData.max_area}&category_id=${searchData.category}&type_id=${searchData.type}&start_date=${searchData.startDate}&end_date=${searchData.endDate}`,
                    token: token,
                    withCredentials: true,
                    contentType: false,
                });
                debugger;
                dispatch(AnalyticsActions.addAnalyticsData(response.data.rows));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

export const getShortenedTypesThunk =
    (token: string): Thunk<AnalyticsActionsType> =>
    async dispatch => {
        try {
            if (token === "") return;
            const response: AxiosResponse<GetTypes> = await api.getApi({
                url: `admin/get-all-types`,
                token: token,
                withCredentials: true,
                contentType: false,
            });
            dispatch(AnalyticsActions.addShortenedTypes(response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };

    

// Types
type GetResposeType = {
    rows: [ {id: number; count: string; absolute_name: string;} ];
};

type GetTypes = {
    rows: [ {id: number; name: string;} ];
};